

.header-sticky {
    position: fixed!important;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 10;
    transition: all .5s ease-in;
}
.header {
    border-bottom: 1px solid #C7C7C7;
    padding: 10px 40px;
    margin-bottom: 35px;
    background-color: #ffffff;
    min-height: 60px;
    @media (max-width: 1200px) {
       padding: 10px 15px;
    }
    &__inner,
    &__right-indent {
        @include flex-c;
    }
    &__inner {
        @media (max-width: 768px) {
           justify-content: space-between;
        }
    }
    &__right-indent {
        margin-left: auto;
        @media (max-width: 768px) {
            position: absolute;
            left: 0;
            right: 0;
            top: 60px;
            width: 100%;
            height: 100vh;
            display: flex;
            flex-direction: column;
            background-color: #E5E5E5;
            padding-bottom: 92px;
            transform: translateX(-100%);
            opacity: 0;
            transition: all .8s ease-in-out;
            z-index: 100;
            nav {
                width: 100%;
            }
        }
    }
    &__logo {
        font-size: 20px;
        color: $mainClr;
        font-weight: 700;
        line-height: 26px;
    }
    &__help {
        font-size: 15px;
        color: $mainClr;
        font-weight: 700;
        line-height: 19.5px;
        margin-left: 60px;
        @media (max-width: 768px) {
           display: none;
        }
    }
    &__profile {
        display: flex;
        align-items: center;
        margin-top: auto;
        @media (max-width: 768px) {
            width: 100%;
        }
    }
    &__profile-desc {
        display: none;
        @media (max-width: 768px) {
           display: block;
        }
    }
    &__profile-img {
        display: inline-block;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        margin-left: 25px;
        overflow: hidden;
        @media (max-width: 768px) {
           width: 80px;
           height: 80px;
           margin-right: 16px;
        }
    }
    &__profile-name,
    &__profile-log-out {
        display: block;
        font-size: 15px;
        font-weight: 700;
        line-height: 19px;
        text-transform: capitalize;
    }
    &__profile-name {
        color: $mainClr;
        margin-bottom: 8px;
    }
    &__profile-log-out {
        color: $brand;
    }
}
.menu {
    margin-left: auto;
    &__list {
        @include flex-b-c;
        @media (max-width: 768px) {
           flex-direction: column;
           justify-content: start;
           width: 100%;
           padding: 20px 15px;
           li {
            margin-top: 24px;
            margin-left: 16px;
            width: 100%;
            .menu__link {
                font-size: 24px;
                line-height: 31.2px;
            }
        }
        }
    }
    &__link {
        @include flex-i-c;
        font-size: 15px;
        color: $mainClr;
        font-weight: 700;
        line-height: 19.5px;
        padding: 0 12.5px;
        transition: all .3s ease-in;
        &:hover {
            border-radius: 4px;
            color: $grey;
        }
        // @media (max-width: 1120px) {
        //     padding: 0 20px;
        //     height: 36px;
        // }
    }
}


.header__right-indent-active {
    transform: translateX(0);
    opacity: 1;
    transition: all .8s ease-in-out;
}
.menu__icon-close {
     span {
        transition: all .3s ease-in-out;
        &:nth-child(1){
            transform: rotate(-45deg);
            top: 7px!important;
        }
        &:nth-child(2){
            opacity: 0;
            left: -50px;
        }
        &:nth-child(3){
            transform: rotate(45deg);
            bottom: 5px!important;
        }
    }
}

@media (max-width: 768px) {
    .menu__icon {
        width: 16px;
        height: 14px;
        margin: 12px 15px;
        z-index: 10;
        transition: .5s ease-in-out;
        transform: rotate(0);
        cursor: pointer;
        span {
            position: absolute;
            left: 0;
            width: 100%;
            height: 2px;
            border-radius: 1px;
            background: $mainClr;
            display: block;
            opacity: 1;
            transition: .25s ease-in-out;
            transform: rotate(0);
            &:nth-child(1) {
                top:  0;
            }
            &:nth-child(2) {
                top: 50%;
                transform: translateY(-50%);
            }
            &:nth-child(3) {
                bottom: 0;
            }
        }
    }
}


