@mixin svg-c {
	position: absolute;
	content: '';
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	display: inline-block;
	z-index: 100;
}
@mixin pos-w {
	position: absolute;
    content: '';
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 100%;
	
}
@mixin pos {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
}
@mixin pos-c {
    position: absolute;
    content: '';
    top: 50%;
    left: 50%;
    transform: translate(-50% -50%);
}
@mixin flex-c {
	display: flex;
	align-items: center;
}
@mixin flex-b-c {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
@mixin flex-b-s {
	display: flex;
	justify-content: space-between;
	align-items: stretch;
}
@mixin  flex-c-c {
	display: flex;
	justify-content: center;
	align-items: center;
}
@mixin flex-b {
	display: flex;
	justify-content: space-between;
}
@mixin flex-i-c {
	display: inline-flex;
	justify-content: center;
	align-items: center;
}
$maxWidth: 1460;

@mixin adaptFont ($mobSize, $pcSize) {
	$addSize: $pcSize - $mobSize;
	$addMobSize: $addSize + $addSize * 0.7;
	@media (max-width: 767px) {
		font-size: calc(#{$mobSize + px} + #{$addMobSize} * ((100vw - 320px)/ #{$maxWidth}));
	}
	@media (min-width: 767px) {
		font-size: calc(#{$mobSize + px} + #{$addSize} * (100vw / #{$maxWidth}));
	}
}


@mixin anim {
    animation-name: slideBottom;
    animation-timing-function: linear;
    animation-fill-mode: both;
    animation-duration: 1s;
  }

  @keyframes slideBottom {
    0% {
      transform: translateY(100%);
      opacity: 0;
    }
    100% {
      transform: translateY(0);
      opacity: 1;
    }
  }
