.main {
    &__inner {
        @include flex-b;
        margin: 0 -25px;
        flex-wrap: wrap;
        @media (max-width: 1200px) {
           margin: 0 -16px;
        }

    }
    &__back-link {
        display: block;
        position: relative;
        color: $mainClr;
        font-weight: 700;
        text-transform: capitalize;
        line-height: 130%;
        padding-left: 52px;
        margin: 32px 0 53px 0;
        @media (max-width: 850px) {
           margin: 0 0 42px;
        }
        .arrow_icon {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            width: 40px;
            height: 40px;
            border: 1px solid #C7C7C7;
            border-radius: 50%;
            @include flex-i-c;
            svg {
                width: 20px;
                height: 20px;
                stroke: $mainClr;
                fill: transparent;   
            }
        }
    }
    &__aside {
        width: 28%;
        padding: 0 25px;
        @media (max-width: 1200px) {
           padding: 0 16px;
        }
        @media (max-width: 850px) {
          width: 100%;
        }
    }
    &__right-indent {
        flex: auto;
        padding: 0 25px;
        @media (max-width: 1200px) {
            padding: 0 16px;
         }
    }
    &__aside-list {
        padding: 20px 0;
        border: 1px solid #C7C7C7;
        border-radius: 20px;
        li {
            margin: 0 20px 24px 0;
            &:last-child {
                margin: 0;
            }
        }
        @media (max-width: 850px) {
            display: none;
         }
    }
    &__aside-link {
        transition: all .3s linear;
        border-left: 2px solid transparent;
        line-height: 130%;
        color: $mainClr;
        font-weight: 700;
        display: block;
        padding-left: 20px;
        transition: all .3s ease-in;
        &:hover {
            border-color: $brand;
            color: $brand;
        }
    }
}
.main__right-indent {
    h3 {
        font-size: 24px;
        line-height: 31.2px;
        color: $mainClr;
        font-weight: 700;
        margin-bottom: 48px;
        @media (max-width: 620px) {
           margin-bottom: 40px;
        }
    }
}
.settings {
    border: 1px solid #C7C7C7;
    border-radius: 20px;
    padding: 20px;
    @media (max-width: 425px) {
       padding: 16px;
       margin: 0 -8px;
    }
    h4 {
        font-size: 20px;
        font-weight: 700;
        line-height: 26px;
        margin-bottom: 12px;
    }
    h6 {
        font-size: 15px;
        line-height: 22.5px;
        font-weight: 500;
        color: #818181;
        margin: 0 0 40px 0;
    }
    p {
        font-size: 11px;
        font-weight: 500;
        line-height: 14px;
        text-transform: uppercase;
        margin-top: 0;
    }
    &__types {
        @include flex-b;
        flex-wrap: wrap;
        @media (max-width: 620px) {
           justify-content: flex-start;
        }
    }
    &__inputs {
        width: 60%;
        @media (max-width: 620px) {
           width: 100%;
        }
    }
    &__input {
        padding-right: 30px;
        p {
            line-height: 22.5px;
            font-weight: 500;
            text-transform: initial;
            color: #818181;
            margin-bottom: 24px;
            margin-left: 32px;
            @media (max-width: 850px) {
               margin-bottom: 20px;
            }
        }
    }

    [type="radio"]:checked,
    [type="radio"]:not(:checked) {
        position: absolute;
        left: -9999px;
    }
    [type="radio"]:checked + label,
    [type="radio"]:not(:checked) + label {
        position: relative;
        padding-left: 32px;
        cursor: pointer;
        display: inline-block;
        font-size: 15px;
        font-weight: 700;
        line-height: 20px;
        margin-bottom: 8px;
    }
    [type="radio"]:checked + label:before,
    [type="radio"]:not(:checked) + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 16px;
        height: 16px;
        border: 2px solid #C7C7C7;
        border-radius: 100%;
        background: #fff;
    }
    [type="radio"]:checked + label:after,
    [type="radio"]:not(:checked) + label:after {
        content: '';
        width: 8px;
        height: 8px;
        background: $brand;
        position: absolute;
        top: 6px;
        left: 6px;
        border-radius: 100%;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
    }
    [type="radio"]:not(:checked) + label:after {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }
    [type="radio"]:checked + label:after {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
    &__btns {
        display: flex;
        justify-content: flex-end;
        margin: 8px 0 5px 0;
        .btn {
            margin-left: 12px;
        }
    }
}
.main__btn {
    text-align: center;
    margin-top: 25%;
    @media (max-width: 850px) {
        display: none;
     }
}
.btn-dark {
    background: #1C1E20;
    max-width: 600px;
    color: #fff;
    position: relative;
    width: 100%;
    text-align: left;
    justify-content: flex-start;
    padding-left: 20px;
    svg {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        fill: #fff;
    }
}

 .password:checked ~ .password__block {
    margin-top: -4px;
    opacity: 1;
    transition: all .3s ease-in;
}
.password{
    &__block {
        display: block;
        flex-direction: column;
        border-bottom: 1px solid#C7C7C7;
        margin: -100px 0 40px 32px;
        opacity: 0;
        transition: all .3s ease-in;
        label,
        input {
            display: block;
            width: 100%;
            margin-bottom: 8px;
        }
        label {
            font-size: 13px;
            font-weight: 400;
            line-height: 17px;
        }
        input {
            border: none;
            background-color: transparent;
            &:focus {
                border-bottom: 1px solid $brand;
            }
        }
    }
    
}