body,
html {
    font-family: $rale;
    font-size: 15px;
    font-weight: 500;
    line-height: 22.5px;
    color: #1C1E20;
    ;
}
*{
    box-sizing: border-box;
}
*:focus{
    outline: none;
}
a {
    text-decoration: none;
    cursor: pointer;
}
h1,
h2,
h3,
h4,
h5,
h6 + p {
    margin-top: 0;
}
p {
    margin: 15px 0;
}
img {
    max-width: 100%;
    width: 100%;
    object-fit: cover;
}
ul {
    margin: 0;
    padding: 0;
    li {
        list-style: none;
    }
}
svg {
    transition: .3s;
}
button {
    border: none;
}
button,
input,
select,
textarea {
    font-family: inherit;
}

$grey: #818181;
$mainClr: #1C1E20;
$brand: #4D1BC1;



section {
    padding: 100px 0;
    @media (max-width: 768px) {
       padding: 70px 0;
    }
    @media (max-width: 425px) {
       padding: 50px 0;
    }
    @media (max-width: 320px) {
       padding: 30px 0;
    }
}
.container {
    max-width: 1232px;
    padding: 0 16px;
    margin: 0 auto;
}
.container-fluid {
    max-width: 100%;
    padding: 0;
}


.btn {
    @include flex-i-c;
    height: 40px;
    background-color: #ffffff;
    border: 1px solid #C7C7C7;
    font-size: 15px;
    line-height: 18px;
    color: $mainClr;
    font-weight: 700;
    text-transform: capitalize;
    font-family: $rale;
    padding: 0 15px;
    white-space: nowrap;
    border-radius: 20px;
}
.btn-purple {
    background: $brand;
    color: #ffffff;
    border: 1px solid $brand;

}
.breadcrumb {
    &__list {
        @include flex-c;
        margin-bottom: 10px;
        li {
            position: relative;
            padding-right: 15px;
            margin-right: 5px;
            line-height: 16.9px;
            &:after {
                position: absolute;
                content: '/';
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                color: $mainClr;
            }
            &:last-child {
                &:after {
                    display: none;
                }
            }
        }
    }
    &__link {
        color: $mainClr;
        font-size: 13px;
        font-weight: 400;
        text-transform: capitalize;
        line-height: 16.9px;
    }

}

 
  .anim-items img{
      transition-delay: .6s;
      transition: all .8s linear;
      transition: 1.2s;  
      will-change: transform;
  }

 